.App {
    text-align: center;
    width: 100vw;
}

body {
    background-image: url('./img/background.jpg');
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.icons {
    display: block;
    width: 100%;
}

.icon {
    display: inline-block;
    width: 15vw;
    height: 15vw;
    background-size: cover;
    background-repeat: no-repeat;
}

.icon-container {
    display: inline-block;
    border: #000000 0px;
    margin: 2px 2px;
    padding: 2vw 1vw;
    height: 15vw;
    background: rgba(255, 255, 255, 0.3)
}

.button-container {
    display: inline-block;
    width: 7vw;
}

.icon-count {
    color: white;
    font-size: 9vw;
    text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
    line-height: 15vw;
    font-weight: bold;
}

.question-mark-counter {
    color: white;
    font-size: 3vw;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    line-height: 0px;
    font-weight: bold;
    vertical-align: super;
    position: relative;
    top: -3vw;
}

.btn {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    color: black;
    padding: 0;
    font-size: 5vw;
    cursor: pointer;
    position: relative;
    top: -1vw;
}

.reset-button-container {
    display: block;
    width: 100%;
    padding: 20px 0;
}

.reset-button-container > button {
    display: inline-block;
    font-size: 16px;
    padding: 15px;
    color: white;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: firebrick;
}

